.logos {
  position: relative;
  z-index: 2;
  align-items: center;
  justify-content: center;
  @include break-min(769px) {
    padding: 20px 2.5%; }
  @include break-max(768px) {
    padding: 20px 20px 0; }

  .flex {
    @include desktop {
      align-items: center;
      justify-content: center;
      width: 100%; } }

  h2.only-mobile {
    display: block;
    width: 50%;
    @include break-max(350px) {
      font-size: rem(20px); } } }

.join-title {
  color: $c0;
  text-align: left;
  font-family: default-font(700);
  @include break-min(240px) {
    font-size: rem(24px); }
  @include break-max(768px) {
    margin: 20px auto;
    text-align: center;
    font-size: rem(20px); }
  @include break-min(769px) {
    font-size: rem(36px); }
  @include desktop {
    width: 500px;
    margin-left: 20px; } }

h2 {
  color: $c0;
  text-align: center;
  font-family: default-font(700); }

.wwfl-link {
  position: relative;
  width: 40%;
  max-width: 100px;

  &::before {
    position: absolute;
    left: -11%;
    height: 100%;
    width: 2px;
    background: $c0;
    border-radius: 4px;
    content: ''; } }

.wwf-link {
  display: inline;
  max-width: 320px;
  @include break-min(769px) {
    margin-left: 10px; }
  @include break-max(769px) {
    max-width: 150px; } }

img.wwf {
  width: 100%;
  height: auto; }

.wwfl-logo {
  position: relative;
  width: 48%;
  padding: 0 0 0 20px;
  @include break-max(769px) {
    width: 100%;
    padding: 0 0 16px 0; }

  img {
    width: 100%;
    height: auto; } }

.wwfl {
  max-width: 234px;
  height: auto; }


.white-bar {
  position: absolute;
  left: calc(54% + 9px);
  top: 0;
  width: 2px;
  height: 80px;
  background: $c0;
  border-radius: 4px; }
